.popup__container {
  background: white;
  color: black;
  max-width: 430px;
  width: 100%;
  border-radius: 10px;
  position: relative;
  padding: 34px 36px 37px 36px;
  box-sizing: border-box;
}

@media(max-width: 600px) {
  .popup__container {
    max-width: 282px;
    padding: 25px 22px;
  }
}
